/**
* @Author: duxin
* description: blog首页
*/

<template>
  <div>
    <HomeArticle v-for="(item,index) in blogList" :key="index" :data="item" @onItemClick="onItemClick"></HomeArticle>
  </div>
</template>

<script>
  import HomeArticle from "../../components/blog-home-article"
  import blogs from "../../blog"

  export default {
    name: "home",
    components: {
      HomeArticle
    },
    data() {
      return {
        blogList: []
      }
    },
    mounted() {
      this.init()
    },
    methods: {
      onItemClick(mdPath) {
        this.$router.push({name: "article", query: {mdPath: mdPath}})
      },
      init() {
        this.blogList = blogs.blogs
      }
    }
  }
</script>

<style scoped>

</style>



