/**
* @Author: duxin
* description: 首页文章条目
*/

<template>
  <div class="article-wrap">
    <p class="article-title" @click="onTitleClick">{{data.title}}</p>
    <div class="article-subhead">
      <span>[置顶]&nbsp;|&nbsp;</span>
      <span><i class="iconfont icon-tubiao204"></i> {{data.createTime}}</span>
    </div>
    <p>{{data.content}}</p>
  </div>
</template>

<script>
  export default {
    name: "blog-home-article",
    props: {
      data: {
        type: Object,
        default: () => {
          return {}
        }
      }
    },
    methods: {
      onTitleClick() {
        this.$emit("onItemClick",this.data.mdPath)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .article-wrap {
    width: 700px;
    padding: 25px;
    border-radius: 4px;
    box-shadow: 0 0 20px rgba(206, 211, 200, 1) inset;
    border: 1px solid rgba(206, 211, 200, 1);
    margin-bottom: 40px;
    background: rgba(206, 211, 200, 0.84);
  }

  .article-title {
    color: #40759b;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    &:hover{
      text-decoration: underline;
    }
  }

  .article-subhead {
    display: flex;
    align-items: center;
    color: #333;
  }

  .iconfont {
    font-size: 18px;
  }
</style>



